/* Normal */
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    background-color: rgb(54, 54, 54);
}
li{
    margin-left: 30px;
}
.title{
    color: black;
    margin-bottom: 8px;
}

/* Navbar */
.Navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: rgb(44, 44, 44);
    color: rgb(230, 187, 0);
    padding: 15px;
    margin-bottom: 20px;
}
._item{
    color: rgb(230, 187, 0);

    text-decoration: none;
    font-size: 20px;
    padding: 0 15px;
}
._item:hover{
    color: rgb(139, 139, 139);
    transition: 0.4s;
}

/* Resume */
.Resume{
    width: 900px;
    margin: 0 auto 30px auto;
    padding: 25px;

    color: rgb(150, 150, 150);
    font-size: 20px;

    border: 3px solid  rgb(44, 44, 44);
    border-radius: 180px;

    display: grid;
    row-gap: 30px;
    column-gap: 10px;
    grid-template-columns: 1fr 2fr;
    grid-template-areas:
    "img name"
    "contact obj"
    "skills ed"
    "act exp";
}
.Resume_img{
    grid-area: img;

    width: 300px;
    border-radius: 150px;  
}
.Resume_name{
    grid-area: name;

    font-size: 80px;
    padding: 50px;

    /* color: rgb(161, 48, 48); */
    color: rgb(230, 187, 0);
}
.Resume_contact{
    grid-area: contact;
}
.Resume_objtive{
    grid-area: obj;
}
.Resume_skills{
    grid-area: skills;
    padding-top: 5px;
}
.Resume_education{
    grid-area: ed;

    padding-top: 45px;
}
.Resume_activities{
    grid-area: act;

    padding-top: 10px;
}
.Resume_exp{
    grid-area: exp;
}
.icon{
    width: 20px;
    margin-right: 10px;
}

