/* Assignments */
.Assignments{
    width: 1000px;
    margin: 0 auto;
}
.Assignments > h2{
    color: blanchedalmond;

    margin-bottom: 20px;
}
.Assign_items > video{
    display: block;

    margin-bottom: 50px;

    border-radius: 50px;
}
.Assign_items > video:focus{
    outline: none;
}